.backdrop {
  z-index: 100000;
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity var(--transition-dur-and-func), visibility var(--transition-dur-and-func);
  background: rgba(91, 63, 63, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(0);
}

.modal {
  max-height: 100%;
  width: 280px;
  border: 2px solid var(--accent-2);
  background-color: #fcfcfc;
  border-radius: 10px;
  padding: 28px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%)scale(1);
}

.modal::-webkit-scrollbar {
  width: 5px;
}

.modal__close-btn {
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  padding: 0;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 11px;
  right: 11px;
  transform: scale(1);
}

.modal__close-btn:hover {
  transform: scale(1.2);
}

